<template>
  <div class="sun-loading" v-show="loading" style="background-color: rgba(0, 0, 0, 0.5);">
    <div class="sun-image"></div>
    <span class="sun-message">{{parentMessage}}</span>
  </div>
</template>

<script>
  export default {
    name: 'loading',
    data() {
      return {
        loading: false,
        parentMessage:'拼命加载中'
      }
    },
    created() {
      let that = this;
      this.bus.$on('loading', function(data,SetMessage) {
        that.parentMessage = SetMessage
        that.loading = !!data;
      })
    }
  }
</script>
<style lang="scss">
  .sun-loading{
    .sun-image{
      width: 45px;
      height: 45px;
      display: block;
      animation: sunLoading 1s steps(12, end) infinite;
      background: transparent 	url('data:image/gif;base64,');
      background-size: 100%;
      margin:auto;
      position:absolute;
      top: 0;
      left: 0;
      bottom: 0;
      right:0;
    }
    .sun-message{
      width: 100%;
      height: 30px;
      color: #2c3e50;
      font-size: 16px;
      margin:auto;
      position:absolute;
      top: 100px;
      left: 0;
      bottom: 0;
      right:0;
    }
  }
</style>
